import React, { Component } from 'react'

import Logo from '../Images/logo.png'

import {Link, NavLink, withRouter} from 'react-router-dom'

class Layout extends Component {
	constructor(props){
		super(props)
		this.state = {
			newClass: ''
		}

	}
	componentWillReceiveProps(nextProps){
		console.log(nextProps)
		if(nextProps.location.pathname === '/about'){
			this.setState({
				newClass: ' white'
			})
		}else{
			this.setState({
				newClass: ''
			})
		}
	}
	render() {
	    return (
	      <div className='nav-container'>
	       	<div className='left'>
	       		<Link to='/'>
		       		<div className='logo-container'>
		       			<img src={Logo} />
		       		</div>
	       		</Link>
	       	</div>
	       	<div className='right'>
	       		<NavLink to='/about' className={'nav-text' + this.state.newClass} activeClassName='active'>
	       			About
	       		</NavLink>
	       		<NavLink to='/design-process' className={'nav-text' + this.state.newClass} activeClassName='active'>
	       			Design Process
	       		</NavLink>
	       		<NavLink to='/works' className={'nav-text' + this.state.newClass} activeClassName='active'>
	       			Works
	       		</NavLink>       		
	       	</div>
	      </div>
	    );
  }
}

const Nav = withRouter(Layout)

export default Nav
